import React, { Component } from 'react'
import onboarding1 from '../pics/Onboarding_01.png'
import "./Onboarding.css";
import { MDBCol, MDBContainer, MDBRow, MDBFooter } from "mdbreact";

export default class Onboarding_01 extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             
        }
    }
    
    render() {
        return (
            <div className="container" style={{height:"100vh"}}>
            {/* pic up */}
                <div>
                    <img alt="firstPic" width="100%" src={onboarding1}></img>
                </div>
                <div style={{}} className="textBox">
                    <h3 className="header1" style={{}}>סיוע</h3>
                    <p className="piska" style={{}}>בקש מה שאתה רוצה מהאנשים הרלוונטים שרוצים לעזור.</p>
                </div>
                <div style={{}}>
                    <MDBFooter color="cyan" className="font-small darken-3 pt-0">
                        <MDBContainer>
                            <MDBRow>
                                <MDBCol  className="py-5">
                                    <div className="mb-5 flex-center"></div>
                                </MDBCol>
                            </MDBRow>
                        </MDBContainer>
                    <div className="footer-copyright text-center py-3">
                        <MDBContainer style={{align:"left"}} fluid>
                            <a style={{fontFamily:"sfRegular", fontSize:"18pt"}} onClick={this.props.skipBtn} className="fb-ic">
                              דלג
                            </a>
                            <a onClick={this.props.btnNext} style={{marginLeft:"50%"}} className="pin-ic">
                            <img src="https://img.icons8.com/ios/50/000000/long-arrow-right.png"/>
                            </a>
                        </MDBContainer>
                    </div>
                    </MDBFooter>
                </div>
            </div>
        )
    }
}
