import { Image, Item } from 'semantic-ui-react'
import React, { Component } from 'react'
import lamp from '../pics/lamp.png'
import location from '../pics/location.png'
import arrow from '../pics/arrow.png'
import clock from '../pics/clock.png'
import 'bootstrap/dist/css/bootstrap.min.css';
import '../semantic/dist/semantic.css'
import postJson from './postsJson.json'
import cities_data from './israel-cities'
import Case from '../pics/Category Icons/Case.svg'
import emergency from '../pics/Category Icons/Emergency.svg'
import lostFound from '../pics/Category Icons/Lost and found.svg'
import General from '../pics/Category Icons/General.svg'
import givAway from '../pics/Category Icons/Give away.svg'
import ItemNeeded from '../pics/Category Icons/Item needed.svg'
import Ride_Delivery from '../pics/Category Icons/Ride_Delivery.svg'
import RoadAssist from '../pics/Category Icons/Road assist.svg'
import Social from '../pics/Category Icons/Social.svg'


export default class RecentPosts extends Component {
  // translate which city by number
  findCity(num){
    return cities_data.find(city=>city.id==num).name
  }

  findTime(timeNum){
    const time= new Date(timeNum)
    const hours= time.getHours();
    const days= time.getDay();
    // time post display
    switch(days) {
      case 0:
        var totalTime= hours + " hours ago"
        return totalTime
      case 1:
        return "1 day ago and" 
      default:
        return "1 day ago"
    }
  }
  findCategory(num){
    switch (num){
      case 0:
        return "Emergency"
      case 1:
        return "Medical"
      case 2:
        return "Ride/Delivery"
        case 3:
        return "Road assistant"
      case 4:
        return "Item needed"
      case 5:
        return "Give away item"
        case 6:
        return "Lost and found"
      case 7:
        return "Social"
      case 8:
        return "General"
    }
  }
  categoryPic=(num)=>{
    switch (num){
      case 0:
        return emergency
      case 1:
        return Case
      case 2:
        return Ride_Delivery
        case 3:
        return RoadAssist
      case 4:
        return ItemNeeded
      case 5:
        return givAway
        case 6:
        return lostFound
      case 7:
        return Social
      case 8:
        return General
    }
   }

  render() {
   
    return (
      <div>
      {postJson.map(user=>{return <div key={user.id}>
        <Item.Group>
          <Item>
            <Item.Content>
              <div style={{float:"left"}}>
                <img src={this.categoryPic(user.categoryId)} style={{width:"20px",paddingRight:5}}/>
                </div>
                <div style={{marginLeft:25}}>
                <Item.Header style={{fontSize:"15pt"}} as='a'>{this.findCategory(user.categoryId)}</Item.Header>
                <Item.Meta><p style={{display:"-webkit-box", WebkitLineClamp:2, WebkitBoxOrient:"vertical", overflow:"hidden", textAlign:"right", direction:"rtl", fontSize:"12pt"}}>{user.content}</p></Item.Meta>
                <Item.Description>
                {/* extra discription */}
                </Item.Description>
                <Item.Extra>
                    <div style={{}} className="ui container">
                      <div style={{fontSize:"10pt" ,width:"100%", alignContent:"center", alignItems:"center"}} className="icons">
                          <img width="15px" src={location} alt="location"/><span style={{marginRight:"10px"}}>{this.findCity(user.city)}</span>
                          <img width="15px" src={arrow} alt="location"/><span style={{marginRight:"10px"}}>distance</span>
                          <img width="15px" src={clock} alt="location"/>{this.findTime(user.createDate)}
                      </div>
                    </div>
                <hr/>

                </Item.Extra>
                </div>
              </Item.Content>
          </Item>  
        </Item.Group>
      </div>
      })}

      {/* end container */}
      </div>
    )
  }
}


